























import Vue from 'vue';
import {Component} from 'vue-property-decorator';
import Tabs from '@/components/Tabs.vue';
import dayjs from 'dayjs';
import clone from '@/lib/clone';
import typeList from '@/constants/typeList';

@Component({
  components: { Tabs },

})
export default class statistics extends Vue {
  // eslint-disable-next-line no-undef
  tagString(tags: Tag[]) {
    return tags.length === 0 ? '无' : tags.map(t => t.name).join('，');
  }

  beautify(string: string) {
    const api = dayjs(string);
    const now = dayjs();
    if (api.isSame(now, 'day')) {
      return '今天';
    } else if (api.isSame(now.subtract(1, 'day'), 'day')) {
      return '昨天';
    } else if (api.isSame(now.subtract(2, 'day'), 'day')) {
      return '前天';
    } else {
      return api.format('月M日D');
    }
  }

  get recordList() {
    // eslint-disable-next-line no-undef
    return this.$store.state.recordList;
  }

  get groupedList() {
    const {recordList} = this;
    if(recordList.length === 0 ){return []}

    // eslint-disable-next-line no-undef
    const newList = clone(recordList as RecordItem[])
        .filter(r => r.types === this.type)
        .sort((a, b) => dayjs(b.createdAt).valueOf() - dayjs(a.createdAt).valueOf());
    if (newList.length === 0) {
      return [] as Result;
    }
    // eslint-disable-next-line no-undef
    type Result = { title: string, items: RecordItem[],total?: number }[]
    const result:Result = [{title: dayjs(newList[0].createdAt).format('YYYY-MM-DD'), items: [newList[0]]}] ;
    // eslint-disable-next-line no-undef
    for (let i = 1; i < newList.length; i++) {
      const current = newList[i];
      const last = result[result.length - 1];
      if (dayjs(last.title).isSame(dayjs(current.createdAt), 'day')) {
        last.items.push(current);
      } else {
        result.push({title: dayjs(current.createdAt).format('YYYY-MM-DD'), items: [current]});
      }
    }
    result.map(group => {
      group.total = group.items.reduce((sum, item) => {
        console.log('sum');
        console.log(sum);
        console.log(item);
        return sum + item.amount;
      }, 0);
    });
    return result;
  }

  beforeCreate() {
    this.$store.commit('fetchRecords');
  }

  type = '-';
  typeList = typeList;
}
